.linkContainer{
  margin-top: 50px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 15px
}

.link{
  text-decoration: none;
  background-color: var(--main-color);
  font-size: 1.5rem;
  padding: 2px 4px 4px 4px;
  border-radius: 15px;
  color: var(--secondary-color);
}

.link:hover{
  background-color: var(--main-hover);
}

.link:active{
  background-color: var(--main-click);
}

.footerLinkContainer{
  margin-top: 25px;
  padding-bottom: 25px;
  display: flex;
  gap: 15px;
  justify-content: center;
}

.footerLinkContainer a:hover{
  opacity: 75%;
  cursor: pointer;
}