.projectContainer{
  background-color: var(--main-color);
  color: var(--secondary-color);
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 25px;
}

.projectHeader{
  text-align: center;
  margin-bottom: 25px;
  font-size: 2rem;
}

.projectContent{
  max-width: 60ch;
  font-size: 1.5rem;
}

.projectLinks{
  margin-top: 25px;
}

.projectLinks a{
  background-color: var(--secondary-color);
  color: var(--main-color);
  padding: 1px 2px 2px 2px;
  border-radius: 5px;
  margin-right: 25px;
  text-decoration: none;
}

.projectLinks a:hover{
  background-color: var(--secondary-hover);
}

.projectLinks a:active{
  background-color: var(--secondary-click);
}