.homeContainer{
  color: var(--secondary-color);
}

.titleContainer{
  margin-top: 50px;
  background-color: var(--main-color);
}

.titleHeader{
  text-align: center;
  font-size: 1.75rem;
}

.titleSubheader{
  text-align: center;
  font-size: 1.5rem;
}

.contactContainer{
  margin-top: 50px;
  background-color: var(--main-color);
  padding-bottom: 75px;
}

.contactHeader{
  text-align: center;
  font-size: 2rem;
}

.contactContent{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.contactContent h3{
  font-size: 1.5rem;
  margin-bottom: 25px;
}

.contactInfo{
  margin-bottom: 50px;
}

.contactForm form{
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contactForm textarea{
  min-height: 125px;
}

#sendButton{
  text-align: left;
}

#sendButton input{
  background-color: var(--secondary-color);
  color: var(--main-color);
  padding: 2px 4px 3px 4px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

#sendButton input:hover{
  background-color: var(--secondary-hover);
}

#sendButton input:active{
  background-color: var(--secondary-click);
}