.aboutContainer{
    color: var(--secondary-color);
}

.aboutContainer h2{
    text-align: center;
    font-size: 2.5rem;
}

.aboutContainer p{
    font-size: 1.5rem;
}

.bioContainer{
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
    margin-bottom: 50px;
}

.bioText{
    background-color: var(--main-color);
    border-radius: 15px;
    max-width: 70ch;
}

.bioText h2{
    margin-top: 25px;
    margin-bottom: 25px;
}

.bioText p{
    margin: 15px;
    line-height: 2rem;
}

.skillsImage img{
    border-radius: 15px;
}

.skillsGrid{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skillsContainer{
    width: 76rem;
    background-color: var(--main-color);
    color: var(--secondary-color);
    border-radius: 15px;
}

.skillsHeader{
    text-align: center;
    font-size: 2rem;
    margin-bottom: 50px;
}

.row{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 5rem;
    margin-bottom: 100px;
}

.skillsIcons{
    display: grid;
    justify-content: center;
    grid-auto-flow: row;
}

.qualificationsContainer{
    background-color: var(--main-color);
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    overflow: auto;
    padding-bottom: 50px;
}

.qualificationContainer{
    display: flex;
    gap: 9.5rem;
    align-items: center;
    flex-wrap: wrap;
}

.qualificationTitle{
    font-size: 1.5rem;
    margin-left: 50px;
    width: 15ch;
}

.qualificationDescription{
    font-size: 1.25rem;
    max-width: 60ch;
}

